.message {
    display: flex;
    flex-direction: column;
    /*border: black solid 2px;*/
    align-items: flex-end;
}

.imageAndText {
    /*border: red solid 2px;*/
    display: flex;
    flex-direction: row-reverse;

}

.imageAndText img {
    width: 50px;
    height: 50px;
    align-self: flex-end;
}

.text {
    /*border: blue solid 2px;*/
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    background-color: #0066cc;
    color: #FFFFFF;
    border-radius: 10px 10px 0px 10px;
    position: relative;
    margin-right: 15px;
}

.text:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 100%;
    display: block;
    width: 25px;
    height: 25px;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    background: radial-gradient(60px at top right,
    transparent 49%, #0066cc 51%) no-repeat right bottom;
}

.name {
    padding-right: 5px;
}

.messageText {
    /*padding-right: 20px;*/
    /*padding-left: 5px;*/
    font-family0: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: white;
    padding-left: 10px;
    padding-right: 50px;
    padding-top: 5px;
    margin-right: 15px;
}

.time {
/*margin-right: 5px;*/
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
    padding-right: 15px;
}

.messageText p {
    word-break: break-all;
    white-space: normal;
    max-width: 500px }