.select {
    display: flex;
    width: 165px;
    height: 24px;
    background: #FFFFFF;
    border: 1px solid #C2C2C2;
    border-radius: 1px;
    font-size: 16px;
    padding-left: 5px;


}

.select:focus {
    background-color: lightgray;
    border: 1px solid #C2C2C2;
    border-radius: 2px;
}

.select:after {

}

.option:hover {

    background: #FFFFFF;
    border: 1px solid #C2C2C2;
    border-radius: 2px;
}