.burgerMenuIcon {
    position: absolute;
    left: 1px;
    margin: 15px 0 0 15px;
}

.header {
    display: flex;
    flex-direction: row;
    position: absolute;
    justify-content: flex-end;
    text-align: center;
    width: 100%;
    height: 60px;
    left: 0px;
    top: 0px;
    background: #FFFFFF;
    box-shadow: 0px 0px 40px rgba(29, 33, 38, 0.13), 0px 0px 2px rgba(29, 33, 38, 0.1);
}

.header h1 {
    position: absolute;
    font-size: 30px;
    color: black;
    text-align: right;
    margin-right: 50px;
    margin-top: 10px;
}