.sidebar {
    position: absolute;
    top: 0;
    left: -100%;
    z-index: 20;
    background-color: white;
    height: 100%;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    box-shadow: 0px 0px 40px rgb(29 33 38 / 13%), 0px 0px 2px rgb(29 33 38 / 10%);
}

.sidebar.open {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
    background-color: white;
    height: 100%;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    box-shadow: 0px 0px 40px rgb(29 33 38 / 13%), 0px 0px 2px rgb(29 33 38 / 10%);
}

.background {
    background-color: white;
}

.nav {
    justify-self: center;
    display: flex;
    flex-direction: column;
    align-self: center;
    align-content: flex-start;
    align-items: flex-start;
    justify-content: center;

}

.nav a {
    display: flex;
    /*width: 250px;*/
    height: 70px;
    flex-direction: column;
    font-size: 30px;
    color: black;
    text-decoration: none;
    text-align: center;
    margin-left: 20px;
    margin-top: 5px;
}

.nav a:hover {
    color: dodgerblue;
}

.nav a.active {
    color: #7fb8f1;
    text-decoration: underline;

}

.close {
    margin: 12px 0 0 300px;
    border: none;
    background: none;
}

